@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.par {
    width: 18vw;
    background-color: black;
    display: flex;
    flex-direction: column;
    ;
    justify-items: center;
    align-items: center;
}

.nav {
    padding-left: 2%;
    margin-top: 5vh;
    height: 70vh;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex;
    gap: 4vh;
}



.navbtn {
    background-color: black;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 5vh;
    gap: 1vw;
    width: 15vw;
    padding-left: 9px;
    cursor: pointer;
}

.actnavbtn {
    background-color: #1e1f24;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 7vh;
    gap: 1vw;
    width: 15vw;
    border-radius: 0.4rem;
    cursor: pointer;
    padding-left: 9px;
}

.navtxt {
    color: gray;
    font-family: 'Poppins', sans-serif;
    font-size: 2.7vh;
}

.actnavtxt {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 2.7vh;
}